import { Component } from "react";
import { FaBed, FaBroom, FaCircleNotch, FaCouch, FaHeart, FaHome, FaLandmark, FaRestroom, FaRulerCombined, FaRulerVertical, FaSquareFull, FaSquarespace, FaStar, FaTape } from "react-icons/fa";
import Fotter from "./Fotter";
import clas from "./Listings.module.css";
import classes from "./RecentListings.module.css";
import 'primeicons/primeicons.css';
import 'primereact/resources/themes/lara-light-indigo/theme.css';
import 'primereact/resources/primereact.css';
import 'primeflex/primeflex.css';
import React, {useRef, useState, useEffect } from 'react';
import { AutoComplete } from 'primereact/autocomplete';
import { CountryService } from '../service/CountryService';
import { Button } from 'primereact/button';
import { About  } from "../navbar/About";
import { StyleClass } from 'primereact/styleclass';

import {Gmap} from "../service/map/Gmaps.js";
import { Data } from "../service/Data";
function Listings(props){
 

   const [countries, setCountries] = useState([]);

   const [selectedCountries, setSelectedCountries] = useState(null);

   const countryservice = new CountryService();

const [filteredCountries, setFilteredCountries] = useState(null);

   useEffect(() => {
       countryservice.getCountries().then(data => setCountries(data));
   }, []); // eslint-disable-line react-hooks/exhaustive-deps

 
   const searchCountry = (event) => {
       setTimeout(() => {
           let _filteredCountries;
           if (!event.query.trim().length) {
               _filteredCountries = [...countries];
           }
           else {
               _filteredCountries = countries.filter((country) => {
                   return country.name.toLowerCase().startsWith(event.query.toLowerCase());
               });
           }

           setFilteredCountries(_filteredCountries);
       }, 250);
   }

 
   const toggleBtnRef = useRef(null);
   const openBtnRef = useRef(null);
   const closeBtnRef = useRef(null);

const [available, setAvailable] = useState(true)

function show() {setAvailable(true) }
function hide(){setAvailable(false)}
return(


<div className={clas.bigList}>
   <div className={clas.abt}>
   {/* props.show && <About show = {props.setshow}/> */ }

</div>


<div className="card">
          
           
          <span className="w-3 p-fluid">
            <AutoComplete id="lined" placeholder="Enter City Ex: Nairobi" className="md:w-full sm:w-full  lg:w-3 m-auto pl-1 " icon="pi pi-user " value={selectedCountries} suggestions={filteredCountries} completeMethod={searchCountry} field="name" multiple onChange={(e) => setSelectedCountries(e.value)} aria-label="Countries" />
          </span>
      </div>


 
 <Data />

<div className="itsbig">
</div>


<StyleClass nodeRef={openBtnRef} selector=".box" enterClassName="hidden" enterActiveClassName="my-fadein">
{!available &&  <Button style={{'background-color':'green'}} onClick={show} ref={openBtnRef} label="Show Map" className="ml-3 mb-5" />
}
</StyleClass>

 <StyleClass  nodeRef={closeBtnRef} selector=".box" leaveActiveClassName="my-fadeout" leaveToClassName="hidden">
 {available &&   <Button style={{'background-color':'red'}} onClick={hide} ref={closeBtnRef  } label="Hide Map" className="mb-5 ml-3" />}
</StyleClass>

<div className="box hidden">
<Gmap />
</div>
 



<Fotter/>

</div>

);
    
}

export default Listings;